import React from 'react';
import moment from 'moment';
// import client, { builder } from '../../client';
const FeaturedPostCard = ({ post }) => (
  <div className="relative h-72">
    <div
      className="absolute rounded-lg bg-center bg-no-repeat bg-cover shadow-md inline-block w-full h-72"
      style={{ backgroundImage: `url('${post.mainImage.asset.url}')` }}
    />
    <div className="absolute rounded-lg bg-center bg-gradient-to-b opacity-50 from-gray-400 via-gray-700 to-black w-full h-72" />
    <div className="flex flex-col rounded-lg p-4 items-center justify-center absolute w-full h-full">
      <p className="text-white mb-4 text-shadow font-semibold text-xs">   
        {moment(post.publishedAt).format('MMM DD, YYYY')}
      </p>
      <p className="text-white mb-4 text-shadow font-semibold text-2xl text-center">
        {post.title}
      </p>
      <div className="flex items-center absolute bottom-5 w-full justify-center">
        <img
          unoptimized
          alt={'shantanu'}
          height="30px"
          width="30px"
          className="align-middle drop-shadow-lg rounded-full"
          src={
            'https://upload.wikimedia.org/wikipedia/commons/0/0f/Facebook_custom_avatar.png'
          }
        />
        <p className="inline align-middle text-white text-shadow ml-2 font-medium">
           shantanu
        </p>
      </div>
    </div>
    <a href={`/post/${post.slug.cureent}`}>
      <span className="cursor-pointer absolute w-full h-full" />
    </a>
  </div>
);

export default FeaturedPostCard;
